
html {
  background-color: #eee;
}
#app {
  font-family: 'Avenir', Helvetica, 'Noto Sans CJK TC', Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  padding: 0 .75rem;
}
a {
  color: #0f7db7;
}
nav ul {
  padding: 0;
}
nav ul li {
    list-style: none;
    display: inline-block;
    padding: 0;
}
nav ul li a {
      font-size: 1.25rem;
      padding: .5rem 2rem;
      color: #0f337d;
      text-decoration: none;
}
.center {
  text-align: center;
}



